<template>
  <div class="text-center">
    <v-menu offset-y open-on-hover>
      <template v-slot:activator="{ on }"  >
        <button
          style="margin-right: 10px"
          v-on="on"
        >
          <i18n-svg height="42px" />
        </button>
      </template>
      <v-list>
        <v-list-item
          v-for="(item, index) in languages"
          :key="index"
          :class="currentLang === item && 'active-lang'"
        >
          <v-list-item-title @click="clickedSave(item)" class="pointer">
            <div class="d-flex align-center">
              <v-img
                class="mr-2"
                max-height="32"
                max-width="41"
                :src="`img/icons/flags/${item}.svg`"
              ></v-img>
              {{$t(`commons.${item}`)}}
            </div>
          </v-list-item-title>
        </v-list-item>
      </v-list>
    </v-menu>
  </div>
</template>

<script>

import I18nSvg from '@public/i18n.svg';
import { mapMutations } from 'vuex';

export default {
  components: {
    I18nSvg,
  },
  data() {
    return {
      languages: ['en', 'es', 'ca', 'eu'],
    };
  },
  computed: {
    currentLang() {
      return this.$i18n.locale;
    },
  },
  methods: {
    ...mapMutations(['changeLocale']),
    clickedSave(id) {
      sessionStorage.helloblocksLocale = id;
      this.$i18n.locale = id;
      this.changeLocale(id);
    },
  },
};

</script>

<style>
.pointer {
  cursor: pointer;
}

.active-lang{
  background-color: var(--v-off-base);
}
</style>
