<template>
  <v-sheet
    max-width= "550"
  >
    <v-slide-group
      v-model="boardValue"
      mandatory
      center-active
      show-arrows
      class="scs-card-board"
    >
      <v-slide-item class="disable-select arduino-uno-btn"
        :key="1"
        :value="boards.arduinoUno"
        v-slot="{ active, toggle }"
      >
        <v-hover
          v-slot="{ hover }"
          close-delay="500"
          open-delay="200"
        >
          <v-card
            :class="active ? 'btn-active' : 'btn-inactive'"
            @click="toggle"
          >
            <div
              v-if="hover"
              class="arduino-uno-btn-hover"
            >
            </div>
            <div class="arduino-uno-icon-content">
              <uno-icon class="arduino-uno-icon"/>
            </div>
            <div class="scs-btn-txt"> Arduino UNO </div>
          </v-card>
        </v-hover>
      </v-slide-item>
      <v-slide-item class="disable-select arduino-nano-btn"
        :key="2"
        :value="boards.arduinoNano"
        v-slot="{ active, toggle }"
      >
        <v-hover
          v-slot="{ hover }"
          close-delay="500"
          open-delay="200"
        >
          <v-card
            :class="active ? 'btn-active' : 'btn-inactive'"
            @click="toggle"
          >
            <div
              v-if="hover"
              class="arduino-nano-btn-hover"
            >
            </div>
            <div class="arduino-nano-icon-content">
              <nano-icon class="arduino-nano-icon"/>
            </div>
              <v-checkbox
                v-model="bootloader"
                class="scs-btn-txt scs-checkbox-icon"
                label="new bootloader"
                value="new"
                hide-details
              ></v-checkbox>
          </v-card>
        </v-hover>
      </v-slide-item>
      <v-slide-item class="disable-select arduino-wemos-btn"
        :key="3"
        :value="boards.arduinoWemosD1"
        v-slot="{ active, toggle }"
      >
        <v-hover
          v-slot="{ hover }"
          close-delay="500"
          open-delay="200"
        >
          <v-card
            :class="active ? 'btn-active' : 'btn-inactive'"
            class="arduino-wemos-btn"
            @click="toggle"
          >
            <div
              v-if="hover"
              class="arduino-wemos-btn-hover"
            >
              Experimental
            </div>
            <div
              class="arduino-wemos-icon-content"
            >
              <wemos-icon class="arduino-wemos-icon"/>
            </div>
            <div class="scs-btn-txt">
              ESP8266 WeMos D1 mini
            </div>
          </v-card>
        </v-hover>
      </v-slide-item>
    </v-slide-group>
  </v-sheet>
</template>
<script>
import { mapGetters } from 'vuex';
import UnoIcon from '@public/img/icons/settings/uno_v0.svg';
import NanoIcon from '@public/img/icons/settings/nano_v0.svg';
import WemosIcon from '@public/img/icons/settings/wemos_v0.svg';
// import BubbleWarningNano from '../actions/bubble-warning-nano.vue';

export default {
  components: {
    UnoIcon,
    NanoIcon,
    WemosIcon,
    // BubbleWarningNano,
  },
  props: {
    scsBoard: String,
  },
  data() {
    return {
      boardValue: this.scsBoard,
      fqbn_arduino_uno: 'arduino:avr:uno',
      fqbn_arduino_nano: 'arduino:avr:nano',
      fqbn_esp8266_d1_mini_clone: 'esp8266:esp8266:d1_mini_clone',
      nanoClicked: false,
      bootloader: '',
      model: null,
    };
  },
  watch: {
    boardValue(newValue) {
      // eslint-disable-next-line no-console
      // console.log('boardValue', newValue);
      this.nanoClicked = newValue === this.boards.arduinoNano;
      const nanoProcessor = (this.bootloader === 'new')
        ? { config: { option: 'cpu', value: 'atmega328' } }
        : { config: { option: 'cpu', value: 'atmega328old' } };
      const config = this.nanoClicked ? nanoProcessor : {};
      if (this.nanoClicked) {
        setTimeout(() => { this.nanoClicked = false; }, 2000);
      }
      this.$emit('update:board', { uuid: newValue, ...config });
    },
    bootloader(newValue) {
      // eslint-disable-next-line no-console
      // console.log('bootloader', newValue);
      const nanoProcessor = (newValue === 'new')
        ? { config: { option: 'cpu', value: 'atmega328' } }
        : { config: { option: 'cpu', value: 'atmega328old' } };
      const config = nanoProcessor;
      this.$emit('update:board', { uuid: this.boards.arduinoNano, ...config });
    },
  },
  computed: {
    ...mapGetters('boards', { findBoards: 'find' }),
    boards() {
      const boards = this.findBoards({ query: { $sort: { name: 1 } } }).data;
      // // eslint-disable-next-line no-console
      // console.log(boards.find((b) => b.name === 'Arduino Nano'));
      // // eslint-disable-next-line no-console
      // console.log(boards.find((b) => b.fqbn === this.fqbn_esp8266_d1_mini_clone));
      return {
        arduinoUno: boards.find((b) => b.fqbn === this.fqbn_arduino_uno)?.uuid,
        arduinoNano: boards.find((b) => b.fqbn === this.fqbn_arduino_nano)?.uuid,
        arduinoWemosD1: boards.find((b) => b.fqbn === this.fqbn_esp8266_d1_mini_clone)?.uuid,
        // arduinoWemosD1: boards.find((b) => b.fqbn === 'esp8266:esp8266:d1_mini_clone')?.uuid,
      };
    },
  },
};
</script>
