<template>
  <v-dialog
    max-width="600"
    overlay-color="background"
    v-model="dialog"
    content-class="editor-modal"
    style="z-index: 1002;"
  >
    <template v-slot:activator="{ on }">
      <div class="tools-item pt-1">
        <button class="ml-2 my-2" v-on="on" @click="handleClick">
          <div class="d-flex align-center">
            <led-icon width="16" />
            <div class="text-body-2 mx-2 white--text">{{$t('toolbar.led-editor')}}</div>
          </div>
        </button>
      </div>
    </template>
    <template>
      <tools-header :text="$t('toolbar.tools')" class="text-sm-body-2" @closed="closeDialog">
        <tool-icon height="25" />
      </tools-header>
      <div class="px-4">
        <div class="led-body mb-4">
          <p class="pt-2 darkText--text text-center font-weight-bold">{{$t('toolbar.led-editor')}}</p>
          <div class="d-flex flex-column justify-center align-center mt-8">
            <matrix :rows="8" :columns="8" ref="matrix" @updatedData="updatedData"/>
            <buttons
              class="d-flex mt-4 justify-space-between px-0"
              :code="code"
              @clear="this.clear"
              @fill="this.fill"
            />
          </div>
          <matrix-textarea class="px-12 pt-4" :code="code"/>
        </div>
      </div>
    </template>
  </v-dialog>
</template>

<script>
import LedIcon from '@public/img/icons/led.svg';
import ToolIcon from '@public/tools.svg';
import ToolsHeader from './header.vue';
import Matrix from './matrix.vue';
import Buttons from './buttons.vue';
import MatrixTextarea from './matrix-textarea.vue';

export default {
  components: {
    MatrixTextarea,
    LedIcon,
    ToolIcon,
    ToolsHeader,
    Matrix,
    Buttons,
  },
  data: () => ({
    dialog: false,
    code: '',
  }),
  methods: {
    clear() {
      this.$refs.matrix.clear();
    },
    fill() {
      this.$refs.matrix.fill();
    },
    updatedData(val) {
      this.code = val;
    },
    closeDialog() {
      this.dialog = false;
    },
    handleClick() {
      this.dialog = true;
      this.$emit('clicked');
    },
  },
};
</script>

<style>
.editor-modal {
  overflow-y: hidden;
  background: var(--v-background-base);
  border-radius: 25px;
  border: 1px solid var(--v-lightGrey-base);
  box-shadow: 8px 8px 8px var(--v-greyShadow-base);
}
.led-body {
  overflow-y: auto;
  max-height: 80vh;
}
.tools-item:hover {
  background: var(--v-toolbox1-lighten1);
}
</style>
