/**
 * @fileoverview Object representing a screenshot.
 * @author ahmad.saad@academiadeinventores.com
 */

import * as Blockly from 'blockly/core';

export let tiggerScreenshot = false;

/**
 * Base64 encoded data uri for screenshot icon.
 * @type {string}
 */

// eslint-disable-next-line max-len
const SCREENSHOT_SVG_DATAURI = 'data:image/png;base64,';

Blockly.Css.register([
  `.blocklyScreenShot {
opacity: .6;
}
.blocklyScreenShotDarken {
opacity: 1.0;
}
.blocklyScreenShot:hover {
opacity: 1.0;
}`,
]);

// eslint-disable-next-line import/prefer-default-export
export class Screenshot {
  /**
   * Class for a Screenshot.
   * @param {!Blockly.WorkspaceSvg} workspace The workspace to sit in.
   * @constructor
   * @implements {Blockly.IAutoHideable}
   * @implements {Blockly.IPositionable}
   */

  /**
   * Constructor for a screenshot.
   * @param {!Blockly.WorkspaceSvg} targetWorkspace The target workspace that
   */
  constructor(targetWorkspace) {
    /**
     * The workspace.
     * @type {!Blockly.WorkspaceSvg}
     * @protected
     */
    this.workspace_ = targetWorkspace;

    /**
     * The unique id for this component.
     * @type {string}
     */
    this.id = 'screenshot';

    /**
     * Array holding info needed to unbind events.
     * Used for disposing.
     * @type {!Array<!Blockly.browserEvents.Data>}
     * @protected
     */
    this.boundEvents_ = [];

    /**
     * Left coordinate of the screenshot.
     * @type {number}
     * @protected
     */
    this.left_ = 0;

    /**
     * Top coordinate of the screenshot.
     * @type {number}
     * @protected
     */
    this.top_ = 0;

    /**
     * Width of the screenshot. Used for clip path.
     * @type {number}
     * @const
     * @protected
     */
    this.WIDTH_ = 64;

    /**
     * Height of the screenshot. Used for clip path.
     * @type {number}
     * @const
     * @protected
     */
    this.HEIGHT_ = 64;

    /**
     * Distance between screenshot and bottom or top edge of workspace.
     * @type {number}
     * @const
     * @protected
     */
    this.MARGIN_VERTICAL_ = 120;

    /**
     * Distance between screenshot and right or left edge of workspace.
     * @type {number}
     * @const
     * @protected
     */
    this.MARGIN_HORIZONTAL_ = 10;

    /**
     * Extent of hotspot on all sides beyond the size of the image.
     * @const {number}
     * @protected
     */
    this.HOTSPOT_MARGIN_ = 10;

    /**
     * The SVG group containing the screenshot.
     * @type {?SVGElement}
     * @protected
     */
    this.svgGroup_ = null;

    /**
     * Top offset for screenshot in svg.
     * @type {number}
     * @private
     */
    this.SPRITE_TOP_ = 0;

    /**
     * Left offset for screenshot in svg.
     * @type {number}
     * @private
     */
    this.SPRITE_LEFT_ = 0;

    /**
     * Width/Height of svg.
     * @type {number}
     * @const
     * @private
     */
    this.SPRITE_SIZE_ = 64;

    /**
     * State of the camera
     * @type {boolean}
     * @const
     * @private
     */
    this.CLICKED = false;
  }

  /**
   * Initializes the Screenshot.
   */
  init() {
    this.workspace_.getComponentManager().addComponent({
      component: this,
      weight: 1,
      capabilities: [Blockly.ComponentManager.Capability.POSITIONABLE],
    });

    this.createDom();
    // this.addEvent_(this.svgGroup_, 'mouseup', this, this.onClick_);
    // this.addEvent_(this.svgGroup_, 'mouseover', this, this.onMouseOver_);
    // this.addEvent_(this.svgGroup_, 'mouseout', this, this.onMouseOut_);

    // Attach listener.
    this.onScreenshot_ = Blockly.browserEvents.conditionalBind(
      this.svgGroup_, 'mouseup', null, this.onClick_.bind(this),
    );

    this.initialized_ = true;
    this.CLICKED = false;
    tiggerScreenshot = false;
  }

  /**
   * Helper method for adding an event.
   * @param {!Element} node Node upon which to listen.
   * @param {string} name Event name to listen to (e.g. 'mousedown').
   * @param {Object} thisObject The value of 'this' in the function.
   * @param {!Function} func Function to call when event is triggered.
   * @private
   */
  addEvent_(node, name, thisObject, func) {
    const event = Blockly.browserEvents.bind(node, name, thisObject, func);
    this.boundEvents_.push(event);
  }

  /**
   * Disposes of workspace search.
   * Unlink from all DOM elements and remove all event listeners
   * to prevent memory leaks.
   */
  dispose() {
    if (this.svgGroup_) {
      Blockly.utils.dom.removeNode(this.svgGroup_);
    }
    if (this.onScreenshot_) {
      Blockly.unbindEvent_(this.onScreenshot_);
    }
  }

  /**
   * Creates DOM for UI element.
   * @protected
   */
  createDom() {
    this.svgGroup_ = Blockly.utils.dom.createSvgElement(Blockly.utils.Svg.G, {}, null);

    const rnd = Blockly.utils.genUid();
    const clip = Blockly.utils.dom.createSvgElement(
      Blockly.utils.Svg.CLIPPATH,
      { id: `blocklyScreenShotClipPath${rnd}` },
      this.svgGroup_,
    );
    Blockly.utils.dom.createSvgElement(
      Blockly.utils.Svg.RECT,
      {
        width: this.WIDTH_,
        height: this.HEIGHT_,
      },
      clip,
    );

    this.svgImg_ = Blockly.utils.dom.createSvgElement(
      Blockly.utils.Svg.IMAGE,
      {
        class: 'blocklyScreenShot',
        'clip-path': `url(#blocklyScreenShotClipPath${rnd})`,
        width: `${this.SPRITE_SIZE_}px`,
        x: -this.SPRITE_LEFT_,
        height: `${this.SPRITE_SIZE_}px`,
        y: -this.SPRITE_TOP_,
      },
      this.svgGroup_,
    );

    this.svgImg_.setAttributeNS(Blockly.utils.dom.XLINK_NS, 'xlink:href', SCREENSHOT_SVG_DATAURI);
    Blockly.utils.dom.insertAfter(this.svgGroup_, this.workspace_.getBubbleCanvas());
  }

  /**
   * Positions the screenshot.
   * It is positioned in the opposite corner to the corner the
   * categories/toolbox starts at.
   * @param {!Blockly.MetricsManager.UiMetrics} metrics The workspace metrics.
   * @param {!Array<!Blockly.utils.Rect>} savedPositions List of rectangles that
   *     are already on the workspace.
   */
  position(metrics) {
    if (!this.initialized_) {
      return;
    }
    const hasVerticalScrollbars = this.workspace_.scrollbar && this.workspace_.scrollbar.canScrollHorizontally();
    const hasHorizontalScrollbars = this.workspace_.scrollbar && this.workspace_.scrollbar.canScrollVertically();

    if (
      metrics.toolboxMetrics.position === Blockly.TOOLBOX_AT_LEFT
      || (this.workspace_.horizontalLayout && !this.workspace_.RTL)
    ) {
      // Right corner placement.
      this.left_ = metrics.absoluteMetrics.left + metrics.viewMetrics.width - this.WIDTH_ - this.MARGIN_HORIZONTAL_;
      if (hasVerticalScrollbars && !this.workspace_.RTL) {
        this.left_ -= Blockly.Scrollbar.scrollbarThickness;
      }
    } else {
      // Left corner placement.
      this.left_ = this.MARGIN_HORIZONTAL_;
      if (hasVerticalScrollbars && this.workspace_.RTL) {
        this.left_ += Blockly.Scrollbar.scrollbarThickness;
      }
    }

    const startAtBottom = metrics.toolboxMetrics.position === Blockly.TOOLBOX_AT_BOTTOM;
    if (startAtBottom) {
      // Bottom corner placement
      this.top_ = metrics.absoluteMetrics.top + metrics.viewMetrics.height - this.HEIGHT_ - this.MARGIN_VERTICAL_;
      if (hasHorizontalScrollbars) {
        // The horizontal scrollbars are always positioned on the bottom.
        this.top_ -= Blockly.Scrollbar.scrollbarThickness;
      }
    } else {
      // Upper corner placement
      this.top_ = metrics.absoluteMetrics.top + this.MARGIN_VERTICAL_;
    }

    this.svgGroup_.setAttribute('transform', `translate(${this.left_},${this.top_})`);
  }

  /**
   * Returns the bounding rectangle of the UI element in pixel units relative to
   * the Blockly injection div.
   * @return {!Blockly.utils.Rect} The components bounding box.
   */
  getBoundingRectangle() {
    return new Blockly.utils.Rect(this.top_, this.top_ + this.HEIGHT_, this.left_, this.left_ + this.WIDTH_);
  }

  /**
   * Handle click event.
   * @param {!MouseEvent} e Mouse event.
   * @protected
   */
  onClick_(e) {
    if (Blockly.utils.isRightButton(e)) {
      return;
    }

    this.CLICKED = true;
    tiggerScreenshot = true;
  }

  /**
   * Convert an SVG datauri into a PNG datauri.
   * @param {string} data SVG datauri.
   * @param {number} width Image width.
   * @param {number} height Image height.
   * @param {!Function} callback Callback.
   */
  svgToPng_(data, width, height, callback) {
    const canvas = document.createElement('canvas');
    const context = canvas.getContext('2d');
    const img = new Image();
    const pixelDensity = 2;

    canvas.width = width * pixelDensity;
    canvas.height = height * pixelDensity;

    // eslint-disable-next-line func-names
    img.onload = function () {
      context.drawImage(img, 0, 0, width, height, 0, 0, canvas.width, canvas.height);
      try {
        const dataUri = canvas.toDataURL('image/png');
        callback(dataUri);
      } catch (err) {
        callback('Error');
      }
    };

    img.src = data;
  }

  /**
   *
   * @description
   * @param {*} url
   */
  getDataUri(url) {
    const image = new Image();
    image.src = url;

    const canvas = document.createElement('canvas');
    canvas.width = image.naturalWidth; // or 'width' if you want a special/scaled size
    canvas.height = image.naturalHeight; // or 'height' if you want a special/scaled size
    canvas.getContext('2d').drawImage(image, 0, 0);

    return canvas.toDataURL('image/png');
  }

  /**
   * Create an SVG of the blocks on the workspace.
   * @param {!Blockly.WorkspaceSvg} workspace The workspace.
   * @param {string=} customCss Custom CSS to append to the SVG.
   *
   */
  workspaceToSvg_(workspace, callback, customCss) {
    /// ///////////////////////////////////////////////////////////////
    // 1.A Declarations
    /// ///////////////////////////////////////////////////////////////
    const bBox = workspace.getBlocksBoundingBox();
    const x = bBox.x || bBox.left;
    const y = bBox.y || bBox.top;
    const width = bBox.width || bBox.right - x;
    const height = bBox.height || bBox.bottom - y;
    const clone = workspace.getCanvas().cloneNode(true);
    const svg = document.createElementNS('http://www.w3.org/2000/svg', 'svg');
    const { length } = clone.querySelectorAll('g>image');
    let url;

    /// ///////////////////////////////////////////////////////////////
    // 1.B
    /// ///////////////////////////////////////////////////////////////
    clone.removeAttribute('transform');

    /// ///////////////////////////////////////////////////////////////
    // 1.C Get all the <image> labels and replace it by data URI
    /// ///////////////////////////////////////////////////////////////
    // eslint-disable-next-line no-plusplus
    for (let i = 0; i < length; i++) {
      url = clone.querySelectorAll('g>image')[i].getAttribute('xlink:href');

      if (url != null) clone.querySelectorAll('g>image')[i].setAttribute('xlink:href', this.getDataUri(url));
    }

    /// ///////////////////////////////////////////////////////////////
    // 1.D Generate the svg atributes
    /// ///////////////////////////////////////////////////////////////
    svg.setAttribute('xmlns', 'http://www.w3.org/2000/svg');
    svg.appendChild(clone);
    svg.setAttribute('viewBox', `${x} ${y} ${width} ${height}`);
    svg.setAttribute(
      'class',
      `blocklySvg ${workspace.options.renderer || 'geras'}-renderer ${
        workspace.getTheme ? `${workspace.getTheme().name}-theme` : ''
      }`,
    );

    svg.setAttribute('width', width);
    svg.setAttribute('height', height);
    svg.setAttribute('style', 'background-color: transparent');

    /// ///////////////////////////////////////////////////////////////
    // 1.E Generate the CSS for the svg
    /// ///////////////////////////////////////////////////////////////
    const css = [].slice
      .call(document.head.querySelectorAll('style'))
      .filter((el) => /\.blocklySvg/.test(el.innerText) || el.id.indexOf('blockly-') === 0)
      .map((el) => el.innerText)
      .join('\n');
    const style = document.createElement('style');
    style.innerHTML = `${css}\n${customCss}`;
    svg.insertBefore(style, svg.firstChild);

    /// ///////////////////////////////////////////////////////////////
    // 1.F Convert to data URI the complete svg
    /// ///////////////////////////////////////////////////////////////
    let svgAsXML = new XMLSerializer().serializeToString(svg);
    svgAsXML = svgAsXML.replace(/&nbsp/g, '&#160');
    const data = `data:image/svg+xml,${encodeURIComponent(svgAsXML)}`;

    /// ///////////////////////////////////////////////////////////////
    // 2. Converting to PNG -> go to 3. callback
    /// ///////////////////////////////////////////////////////////////
    this.svgToPng_(data, width, height, callback);
  }

  /**
   * Close screenshot dialog.
   */
  closeScreenshot() {
    this.CLICKED = false;
    tiggerScreenshot = false;
  }

  /**
   * Download a screenshot of the blocks on a Blockly workspace.
   * @param {!Blockly.WorkspaceSvg} workspace The Blockly workspace.
   * @param {String} Name of the file to be download
   */
  downloadScreenshot(workspace, name) {
    /// ///////////////////////////////////////////////////////////////
    // 1. Convert the current blocks in SVG
    /// ///////////////////////////////////////////////////////////////
    this.workspaceToSvg_(workspace, (datauri) => {
      /// ///////////////////////////////////////////////////////////////
      // 2. Step 2 -> svgToPng_() at the the end of workspaceToSvg_
      /// ///////////////////////////////////////////////////////////////

      /// ///////////////////////////////////////////////////////////////
      // 3. Download the image
      /// ///////////////////////////////////////////////////////////////
      const a = document.createElement('a');

      if (name === '') {
        a.download = `HelloBlocks${Date.now()}.png`;
      } else {
        a.download = `${name}.png`;
      }
      a.target = '_self';
      a.href = datauri;
      document.body.appendChild(a);
      a.click();
      a.parentNode.removeChild(a);
    });

    this.CLICKED = false;
    tiggerScreenshot = false;
  }
}
