import Vue from 'vue';
import Router from 'vue-router';
import NotFound from './views/Error.vue';
import Blockly from './views/Blockly.vue';

Vue.use(Router);

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'home',
      component: Blockly,
    },
    {
      path: '/index.html',
      redirect: '/',
    },
    { path: '*', component: NotFound },
  ],
});
