import ToolIcon from '@public/tools.svg';
import ToolsHeader from './header.vue';

export default {
  components: {
    ColorPickerIcon,
    ToolIcon,
    ToolsHeader,
  },
  data: () => ({
    dialog: false,
    code: '',
  }),
  methods: {
    closeDialog() {
      this.dialog = false;
